import * as actionTypes from '../actions/actions';
import resultNumFn from './Utility/result';

const initialState = {
    topResult:{
        ones: {focusAction: false, value: null, carry: null},
        tens: {focusAction: false, value: null, carry: null},
        hundreds: {focusAction: false, value: null, carry: null},
        thousands: {focusAction: false, value: null, carry: null},
        tenThousands: {focusAction: false, value: null, carry: null},
        hundredThousands: {focusAction: false, value: null, carry: null}
    },
    middleResult:{
        ones: {focusAction: false, value: null, carry: null},
        tens: {focusAction: false, value: null, carry: null},
        hundreds: {focusAction: false, value: null, carry: null},
        thousands: {focusAction: false, value: null, carry: null},
        tenThousands: {focusAction: false, value: null, carry: null},
        hundredThousands: {focusAction: false, value: null, carry: null}
    },
    bottomResult:{
        ones: {focusAction: false, value: null, carry: null},
        tens: {focusAction: false, value: null, carry: null},
        hundreds: {focusAction: false, value: null, carry: null},
        thousands: {focusAction: false, value: null, carry: null},
        tenThousands: {focusAction: false, value: null, carry: null},
        hundredThousands: {focusAction: false, value: null, carry: null}
    },
    finalResult:{
        ones: {focusAction: false, value: null, carry: null},
        tens: {focusAction: false, value: null, carry: null},
        hundreds: {focusAction: false, value: null, carry: null},
        thousands: {focusAction: false, value: null, carry: null},
        tenThousands: {focusAction: false, value: null, carry: null},
        hundredThousands: {focusAction: false, value: null, carry: null}
    },
    focusingInput: {
        t:{
            first: false,
            second: false,
            third: false
        },
        b:{
            first: false,
            second: false,
            third: false
        },
    },
    allTexts: {}
};

const result = (state = initialState, action)=>{
    if(action.type === actionTypes.END){
        return initialState;
    }
    let resultNumb;
    var n1, n2, n3;
    let finalResultNum;
    const finalResultFn = (carry, n1, n2, n3) =>{
        let resultNumber;
        let resultNum;
        let carryingNum;
        const rNumFn = (resultNumber) =>{
            if(resultNumber.toString().length > 1){
                resultNum = resultNumber.toString().slice(1);
                carryingNum = resultNumber.toString().slice(0,1);
            }else{
                resultNum = resultNumber;
            }
            return resultNum;
        }
        if(carry){
            resultNumber = Number(n1) + Number(n2) + Number(n3) + Number(carry);
            resultNum = rNumFn(resultNumber);
        }else{
            resultNumber = Number(n1) + Number(n2) + Number(n3);
            resultNum = rNumFn(resultNumber);
        }
        return {resultNum,carryingNum};
    }
    switch (action.position) {
        case 0:
            resultNumb = resultNumFn(null, action.topRow[2], action.bottomRow[2])
            return{
                ...state,
                topResult:{
                    ...state.topResult,
                    ones:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    0: `${action.topRow[2]}x${action.bottomRow[2]}=${action.topRow[2] * action.bottomRow[2]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        third: true
                    },
                    b:{
                        ...state.focusingInput.b,
                        third: true
                    },
                }
            }
        case 1:
            resultNumb = resultNumFn(state.topResult.ones.carry, action.topRow[1], action.bottomRow[2])
            return{
                ...state,
                topResult:{
                    ...state.topResult,
                    ones:{
                        ...state.topResult.ones,
                        focusAction: false
                    },
                    tens:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    1: `${action.topRow[1]}x${action.bottomRow[2]}=${action.topRow[1] * action.bottomRow[2]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        second: true,
                        third: false
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 2:
            resultNumb = resultNumFn(state.topResult.tens.carry, action.topRow[0], action.bottomRow[2])
            return{
                ...state,
                topResult:{
                    ...state.topResult,
                    tens:{
                        ...state.topResult.tens,
                        focusAction: false
                    },
                    hundreds:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: null
                    },
                    thousands:{
                        focusAction: resultNumb.carryingNum?true:false,
                        value: resultNumb.carryingNum,
                        carry: null
                    },
                },
                allTexts: {
                    ...state.allTexts,
                    2: `${action.topRow[0]}x${action.bottomRow[2]}=${action.topRow[0] * action.bottomRow[2]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        first: true,
                        second: false
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 3:
            return{
                ...state,
                topResult:{
                    ...state.topResult,
                    hundreds:{
                        ...state.topResult.hundreds,
                        focusAction: false
                    },
                    thousands:{
                        ...state.topResult.thousands,
                        focusAction: false
                    }
                },
                middleResult:{
                    ...state.middleResult,
                    ones:{
                        ...state.middleResult.ones,
                        focusAction: true,
                        value: 0
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    3: `Reason for adding 0`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        first: false
                    },
                    b:{
                        ...state.focusingInput.b,
                        third: false,
                        second: true
                    },
                }
            }
        case 4:
            resultNumb = resultNumFn(null, action.topRow[2], action.bottomRow[1])
            return{
                ...state,
                middleResult:{
                    ...state.middleResult,
                    ones:{
                        ...state.middleResult.ones,
                        focusAction: false
                    },
                    tens:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum 
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    4: `${action.topRow[2]}x${action.bottomRow[1]}=${action.topRow[2] * action.bottomRow[1]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        third: true
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 5:
            resultNumb = resultNumFn(state.middleResult.tens.carry, action.topRow[1], action.bottomRow[1])
            return{
                ...state,
                middleResult:{
                    ...state.middleResult,
                    tens:{
                        ...state.middleResult.tens,
                        focusAction: false
                    },
                    hundreds:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum 
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    5: `${action.topRow[1]}x${action.bottomRow[1]}=${action.topRow[1] * action.bottomRow[1]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        third: false,
                        second: true
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 6:
            resultNumb = resultNumFn(state.middleResult.hundreds.carry, action.topRow[0], action.bottomRow[1])
            return{
                ...state,
                middleResult:{
                    ...state.middleResult,
                    hundreds:{
                        ...state.middleResult.hundreds,
                        focusAction: false
                    },
                    thousands:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: null
                    },
                    tenThousands:{
                        focusAction: resultNumb.carryingNum?true:false,
                        value: resultNumb.carryingNum,
                        carry: null
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    6: `${action.topRow[0]}x${action.bottomRow[1]}=${action.topRow[0] * action.bottomRow[1]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        second: false,
                        first: true
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 7:
            return{
                ...state,
                middleResult:{
                    ...state.middleResult,
                    thousands:{
                        ...state.middleResult.thousands,
                        focusAction: false
                    },
                    tenThousands:{
                        ...state.middleResult.tenThousands,
                        focusAction: false
                    }
                },
                bottomResult:{
                    ...state.bottomResult,
                    ones:{
                        ...state.bottomResult.ones,
                        focusAction: true,
                        value: 0
                    },
                    tens:{
                        ...state.bottomResult.tens,
                        focusAction: true,
                        value: 0
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    7: `Reason for adding two 0`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        first: false
                    },
                    b:{
                        ...state.focusingInput.b,
                        second: false,
                        first: true
                    },
                }
            }
        case 8:
            resultNumb = resultNumFn(null, action.topRow[2], action.bottomRow[0])
            return{
                ...state,
                bottomResult:{
                    ...state.bottomResult,
                    ones:{
                        ...state.bottomResult.ones,
                        focusAction: false
                    },
                    tens:{
                        ...state.bottomResult.tens,
                        focusAction: false
                    },
                    hundreds:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    8: `${action.topRow[2]}x${action.bottomRow[0]}=${action.topRow[2] * action.bottomRow[0]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        third: true
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 9:
            resultNumb = resultNumFn(state.bottomResult.hundreds.carry, action.topRow[1], action.bottomRow[0])
            return{
                ...state,
                bottomResult:{
                    ...state.bottomResult,
                    hundreds:{
                        ...state.bottomResult.hundreds,
                        focusAction: false,
                    },
                    thousands:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: resultNumb.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    9: `${action.topRow[1]}x${action.bottomRow[0]}=${action.topRow[1] * action.bottomRow[0]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        second: true,
                        third: false
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 10:
            resultNumb = resultNumFn(state.bottomResult.thousands.carry, action.topRow[0], action.bottomRow[0])
            return{
                ...state,
                bottomResult:{
                    ...state.bottomResult,
                    thousands:{
                        ...state.bottomResult.thousands,
                        focusAction: false,
                    },
                    tenThousands:{
                        focusAction: true,
                        value: resultNumb.resultNum,
                        carry: null
                    },
                    hundredThousands:{
                        focusAction: resultNumb.carryingNum?true:false,
                        value: resultNumb.carryingNum,
                        carry: null
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    10: `${action.topRow[0]}x${action.bottomRow[0]}=${action.topRow[0] * action.bottomRow[0]} and result ${resultNumb.resultNum}, carry ${resultNumb.carryingNum ? resultNumb.carryingNum : 'nothing'}`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        second: false,
                        first: true
                    },
                    b:{
                        ...state.focusingInput.b
                    },
                }
            }
        case 11:
            return{
                ...state,
                bottomResult:{
                    ...state.bottomResult,
                    tenThousands:{
                        ...state.bottomResult.tenThousands,
                        focusAction: false
                    },
                    hundredThousands:{
                        ...state.bottomResult.hundredThousands,
                        focusAction: false
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    11: `Multiplication end! Addition start.`
                },
                focusingInput: {
                    ...state.focusingInput,
                    t:{
                        ...state.focusingInput.t,
                        first: false
                    },
                    b:{
                        ...state.focusingInput.b,
                        first: false
                    },
                }
            }
        case 12:
            n1= state.topResult.ones.value;
            n2= state.middleResult.ones.value;
            n3= state.bottomResult.ones.value;
            finalResultNum = finalResultFn(null, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    ones:{
                        focusAction: true,
                        value: finalResultNum.resultNum,
                        carry: finalResultNum.carryingNum,
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    12: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 13:
            n1= state.topResult.tens.value;
            n2= state.middleResult.tens.value;
            n3= state.bottomResult.tens.value;
            finalResultNum = finalResultFn(state.finalResult.ones.carry, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    ones:{
                        ...state.finalResult.ones,
                        focusAction: false,
                    },
                    tens:{
                        focusAction: true,
                        value: finalResultNum.resultNum,
                        carry: finalResultNum.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    13: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 14:
            n1= state.topResult.hundreds.value;
            n2= state.middleResult.hundreds.value;
            n3= state.bottomResult.hundreds.value;
            finalResultNum = finalResultFn(state.finalResult.tens.carry, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    ones:{
                        ...state.finalResult.ones,
                        carry: null
                    },
                    tens:{
                        ...state.finalResult.tens,
                        focusAction: false,
                    },
                    hundreds:{
                        focusAction: true,
                        value: finalResultNum.resultNum,
                        carry: finalResultNum.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    14: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 15:
            n1= state.topResult.thousands.value;
            n2= state.middleResult.thousands.value;
            n3= state.bottomResult.thousands.value;
            finalResultNum = finalResultFn(state.finalResult.hundreds.carry, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    tens:{
                        ...state.finalResult.tens,
                        carry: null
                    },
                    hundreds:{
                        ...state.finalResult.hundreds,
                        focusAction: false,
                    },
                    thousands:{
                        focusAction: true,
                        value: finalResultNum.resultNum,
                        carry: finalResultNum.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    15: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 16:
            n1= state.topResult.tenThousands.value;
            n2= state.middleResult.tenThousands.value;
            n3= state.bottomResult.tenThousands.value;
            finalResultNum = finalResultFn(state.finalResult.thousands.carry, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    hundreds:{
                        ...state.finalResult.hundreds,
                        carry: null
                    },
                    thousands:{
                        ...state.finalResult.thousands,
                        focusAction: false,
                    },
                    tenThousands:{
                        focusAction: true,
                        value: finalResultNum.resultNum,
                        carry: finalResultNum.carryingNum
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    16: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 17:
            n1= state.topResult.hundredThousands.value;
            n2= state.middleResult.hundredThousands.value;
            n3= state.bottomResult.hundredThousands.value;
            finalResultNum = finalResultFn(state.finalResult.tenThousands.carry, n1?n1:0, n2?n2:0, n3?n3:0)
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    thousands:{
                        ...state.finalResult.thousands,
                        carry: null
                    },
                    tenThousands:{
                        ...state.finalResult.tenThousands,
                        focusAction: false,
                    },
                    hundredThousands:{
                        focusAction: finalResultNum.resultNum === 0? false: true,
                        value: finalResultNum.resultNum === 0? null: finalResultNum.resultNum,
                        carry: null
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    17: `${n1?n1:0}+${n2?n2:0}+${n3?n3:0}=${Number(n1?n1:0)+Number(n2?n2:0)+Number(n3?n3:0)} and result ${finalResultNum.resultNum}, carry ${finalResultNum.carryingNum ? finalResultNum.carryingNum : 'nothing'}`
                }
            }
        case 18:
            return{
                ...state,
                finalResult:{
                    ...state.finalResult,
                    tenThousands:{
                        ...state.finalResult.tenThousands,
                        carry: null
                    },
                    hundredThousands:{
                        ...state.finalResult.hundredThousands,
                        focusAction: false
                    }
                },
                allTexts: {
                    ...state.allTexts,
                    18: `The End`
                }
            }
        default:
            return state
    }
};

export default result;