import * as actionTypes from '../actions/actions';

const initialState = {
    topLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    },
    middleLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    },
    bottomLine:{
        ones: {focusAction: false, value: null},
        tens: {focusAction: false, value: null},
        hundreds: {focusAction: false, value: null},
        thousands: {focusAction: false, value: null},
        tenThousands: {focusAction: false, value: null},
        hundredThousands: {focusAction: false, value: null}
    }
};

const carry = (state = initialState, action)=>{
    if(action.type === actionTypes.END){
        return initialState;
    }
    let carryNumb;
    const carryNumFn = (prev, p1, p2) => {
        let carryNumber;
        let carryNum;
        const cNumFn = (carryNumber) =>{
            if(carryNumber.toString().length > 1){
                carryNum = carryNumber.toString().slice(0,1)
            }else{
                carryNum = null;
            }
            return carryNum;
        }
        if(prev){
            carryNumber = (Number(action.topRow[p1]) * Number(action.bottomRow[p2])) + Number(prev);
            carryNum = cNumFn(carryNumber);
        }else{
            carryNumber = Number(action.topRow[p1]) * Number(action.bottomRow[p2]);
            carryNum = cNumFn(carryNumber);
        }
        return carryNum;
    }
    const stepsFn = (obj) => {
        return {
            ...initialState,
            [obj.line] : {
                ...initialState[obj.line],
                [obj.focusPos] : {
                    focusAction: obj.carryNumb?true:false,
                    value: obj.carryNumb
                }
            }
        }
    }
    switch (action.position) {
        case 0:
            carryNumb = carryNumFn(null,2,2);
            return stepsFn({
                line: 'bottomLine',
                focusPos: 'tens',
                carryNumb: carryNumb
            })

        case 1:
            carryNumb = carryNumFn(state.bottomLine.tens.value,1,2);
            return stepsFn({
                line: 'bottomLine',
                focusPos: 'hundreds',
                carryNumb: carryNumb
            })
        case 2:
            return initialState
        case 4:
            carryNumb = carryNumFn(null, 2,1);
            return stepsFn({
                line: 'middleLine',
                focusPos: 'hundreds',
                carryNumb: carryNumb
            })
        case 5:
            carryNumb = carryNumFn(state.middleLine.hundreds.value, 1,1);
            return stepsFn({
                line: 'middleLine',
                focusPos: 'thousands',
                carryNumb: carryNumb
            })
        case 6:
            return initialState
        case 8:
            carryNumb = carryNumFn(null, 2,0);
            return stepsFn({
                line: 'topLine',
                focusPos: 'thousands',
                carryNumb: carryNumb
            })
        case 9:
            carryNumb = carryNumFn(state.topLine.thousands.value, 1,0);
            return stepsFn({
                line: 'topLine',
                focusPos: 'tenThousands',
                carryNumb: carryNumb
            })
        case 10:
            return initialState
    
        default:
            return state
    }
};

export default carry;