import React from 'react';
import { connect } from 'react-redux';
import Header from '../../components/BoardHeaderRow/Row';
import Carry from '../../components/BoardCarryRow/Row';
import Result from '../../components/BoardResultsRow/Row';
import classNames from './Board.module.css';
import * as reduxFunctions from '../../store/actions/index';

const Board = (props) => {

    const inputChange = (e, i, tb, changeFN)=>{
        let maxLength = 1;
        if (e.target.value.length > maxLength){
            e.target.value = e.target.value.slice(0, maxLength);
        }
        let old = [...tb]
        old[i] = e.target.value;
        props.input(old, changeFN);
    }


    return (
        <div className={classNames.container}>
            <div className={classNames.inner}>
                <Header />

                <Carry numbers={props.tLofCarry} color='c1' />
                <Carry numbers={props.mLofCarry} color='c2' />
                <Carry numbers={props.bLofCarry} color='c3' />

                <div className={classNames.row}>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <h1>{null}</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <h1>{null}</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <h1>{null}</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.tR[0]}
                        onChange={(e)=>inputChange(e, 0, props.tR, 'topRow')} max="1"
                        style={props.focusInput.t.first ? {backgroundColor: 'yellow'} : null}
                        />
                    </div>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.tR[1]}
                        onChange={(e)=>inputChange(e, 1, props.tR, 'topRow')} max="1"
                        style={props.focusInput.t.second ? {backgroundColor: 'yellow'} : null}
                        />
                    </div>
                    <div className={`${classNames.digits} ${classNames.brc}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.tR[2]}
                        onChange={(e)=>inputChange(e, 2, props.tR, 'topRow')}
                        max="1"
                        style={props.focusInput.t.third ? {backgroundColor: 'yellow'} : null}
                        />
                    </div>
                </div>

                <div className={classNames.row}>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <h1>{null}</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <h1>{null}</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <h1>X</h1>
                    </div>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.bR[0]}
                        onChange={(e)=>inputChange(e, 0, props.bR, 'bottomRow')} max="1"
                        style={props.focusInput.b.first ? {backgroundColor: 'yellow', color:'#0070C0'} : {color:'#0070C0'}}
                        />
                    </div>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.bR[1]}
                        onChange={(e)=>inputChange(e, 1, props.bR, 'bottomRow')} max="1"
                        style={props.focusInput.b.second ? {backgroundColor: 'yellow', color:'#00B050'} : {color:'#00B050'}}
                        />
                    </div>
                    <div className={`${classNames.digits} ${classNames.brd}`}>
                        <input
                        id='inputs'
                        type='number'
                        value={props.bR[2]}
                        onChange={(e)=>inputChange(e, 2, props.bR, 'bottomRow')} max="1"
                        style={props.focusInput.b.third ? {backgroundColor: 'yellow', color:'#7030A0'} : {color:'#7030A0'}}
                        />
                    </div>
                </div>

                <Result numbers={props.r1} color='r1' blank fRC={props.r4} />
                <Result numbers={props.r2} color='r2' blankH zeroO />
                <Result numbers={props.r3} color='r3' zero/>
                <Result numbers={props.r4} color='r4'/>
                
            </div>
        </div>
    );
};

const mapStatetoProps = state => {
    return{
        tR: state.number.topRow,
        bR: state.number.bottomRow,
        tLofCarry : state.carry.topLine,
        mLofCarry : state.carry.middleLine,
        bLofCarry : state.carry.bottomLine,
        r1 : state.result.topResult,
        r2 : state.result.middleResult,
        r3 : state.result.bottomResult,
        r4 : state.result.finalResult,
        focusInput: state.result.focusingInput
    }
}
const mapDispatchtoProps = dispatch => {
    return{
        input: (newNum, fn) => dispatch(reduxFunctions.inputNumber(newNum, fn))
    }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Board);