import React, { useState } from "react";
import Button from "../../components/Button/Button";
import classNames from "./Buttons.module.css";
import {connect} from 'react-redux';
import * as reduxFunctions from '../../store/actions/index';

const Buttons = (props) => {
  const [btnHide, setBtnHide] = useState(true);
  const allInputs = document.querySelectorAll('#inputs');

  const starting = () => {
    let inputCheck = true;
    props.tR.forEach(e => {
      if(e === ''){
        inputCheck = false;
      }
    });
    props.bR.forEach(e => {
      if(e === ''){
        inputCheck = false;
      }
    });

    if(inputCheck){
      allInputs.forEach(c=>{
        c.disabled = true;
      })
      setBtnHide(false);
      props.startMulti();
    }else{
      alert('Please fill out all the inputs')
    }
  }


  const ending = () => {
    allInputs.forEach(c=>{
      c.disabled = false;
    })
    setBtnHide(true);
    props.endMulti();
  }

  const setTopAndBelowItems = () => {
    props.random(3, 'topRow');
    props.random(3, 'bottomRow');
  };

  const nextSteps = () => {
    props.countSteps();
    props.carry(props.c, props.tR, props.bR);
    props.result(props.c, props.tR, props.bR);
  }

  return (
    <>
      <div className={classNames.container}>
        {btnHide ? <>
        <Button btnClick={setTopAndBelowItems}>Random</Button>
        <Button btnClick={starting}>Start</Button>
        </>
        : <>
        <Button btnClick={ending}>End</Button>
        {!props.nextBtn? <Button btnClick={nextSteps}>Next</Button> : null}
        </>
        }
      </div>
    </>
  );
};
const mapStatetoProps = state => {
  return{
      tR: state.number.topRow,
      bR: state.number.bottomRow,
      c: state.number.counter,
      nextBtn: state.number.nextBtnHide,
      fR: state.result.finalResult
  }
}

const mapDispatchtoProps = dispatch => {
  return{
      random: (d, fn) => dispatch(reduxFunctions.randomNumberGenerate(d, fn)),
      startMulti: () => dispatch(reduxFunctions.startMultiplication()),
      endMulti: () => dispatch(reduxFunctions.endMultiplication()),
      countSteps: () => dispatch(reduxFunctions.stepCounter()),
      carry: (p, t, b, f) => dispatch(reduxFunctions.carryAddition(p, t, b, f)),
      result: (p, t, b) => dispatch(reduxFunctions.resultAddition(p, t, b))
  }
}

export default connect(mapStatetoProps, mapDispatchtoProps)(Buttons);