import React from 'react';
import classNames from '../../containers/NumberBoard/Board.module.css';

const Row = () => {
    return (
        <div className={`${classNames.row} ${classNames.mobile}`}>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Hundred Thousands</h4>
            </div>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Ten Thousands</h4>
            </div>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Thousands</h4>
            </div>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Hundreds</h4>
            </div>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Tens</h4>
            </div>
            <div className={`${classNames.title} ${classNames.brt}`}>
                <h4>Ones</h4>
            </div>
        </div>
    );
};

export default Row;