const resultNumFn = (prev, p1, p2) => {
    let resultNumber;
    let resultNum;
    let carryingNum;
    const rNumFn = (resultNumber) =>{
        if(resultNumber.toString().length > 1){
            resultNum = resultNumber.toString().slice(1);
            carryingNum = resultNumber.toString().slice(0,1);
        }else{
            resultNum = resultNumber;
        }
        return resultNum;
    }
    if(prev){
        resultNumber = (Number(p1) * Number(p2)) + Number(prev);
        resultNum = rNumFn(resultNumber);
    }else{
        resultNumber = Number(p1) * Number(p2);
        resultNum = rNumFn(resultNumber);
    }
    return {resultNum,carryingNum};
}

export default resultNumFn;