import React from 'react';
import classNames from '../../containers/NumberBoard/Board.module.css';

const Row = (props) => {
    let classes;
    switch (props.color) {
        case 'r1':
            classes = `${classNames.results} ${classNames.r1} ${classNames.brc}`
            break;
        case 'r2':
            classes = `${classNames.results} ${classNames.r2} ${classNames.brc}`
            break;
        case 'r3':
            classes = `${classNames.results} ${classNames.r3} ${classNames.brp}`
            break;
        case 'r4':
            classes = `${classNames.results} ${classNames.r4} ${classNames.brc}`
            break;
    
        default:
            break;
    }
    return (
        <div className={classNames.row}>
            <div className={classes} style={props.numbers.hundredThousands.focusAction ? {backgroundColor: '#ffffc0'} : props.blank || props.blankH ? {backgroundColor: 'white'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.tenThousands.carry}</span>:null}
                <h1>{props.numbers.hundredThousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.tenThousands.focusAction ? {backgroundColor: '#ffffc0'} : props.blank ? {backgroundColor: 'white'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.thousands.carry}</span>:null}
                <h1>{props.numbers.tenThousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.thousands.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.hundreds.carry}</span>:null}
                <h1>{props.numbers.thousands.value}</h1>
            </div>
            <div className={classes} style={props.numbers.hundreds.focusAction ? {backgroundColor: '#ffffc0'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.tens.carry}</span>:null}
                <h1>{props.numbers.hundreds.value}</h1>
            </div>
            <div className={classes} style={props.numbers.tens.focusAction ? {backgroundColor: '#ffffc0'} : props.zero ? {backgroundColor: 'white', color: '#D9D9D9'} : null}>
                {props.fRC ? <span className={classNames.frCarry}>{props.fRC.ones.carry}</span>:null}
                <h1>{props.numbers.tens.value}</h1>
            </div>
            <div className={classes} style={props.numbers.ones.focusAction ? {backgroundColor: '#ffffc0'} : props.zero || props.zeroO ? {backgroundColor: 'white', color: '#D9D9D9'} : null}>
                <h1>{props.numbers.ones.value}</h1>
            </div>
        </div>
    );
};

export default Row;